import React, { useState } from "react";
import "./Tutorial.css";

const tutorials = [
  {
    id: 1,
    category: "HTML",
    title: "HTML Basics",
    description: "Learn the foundations of HTML and how to structure a webpage.",
    videos: [
      { type: "YouTube", url: "https://www.youtube.com/embed/dD2EISBDjWM" },
      { type: "YouTube", url: "https://www.youtube.com/embed/BsDoLVMnmZs" },
      { type: "YouTube", url: "https://www.youtube.com/embed/pQN-pnXPaVg" },
      { type: "YouTube", url: "https://www.youtube.com/embed/UB1O30fR-EE" },
    ],
  },
  {
    id: 2,
    category: "CSS",
    title: "CSS Styling",
    description: "Master the art of styling web pages with CSS.",
    videos: [
      { type: "YouTube", url: "https://www.youtube.com/embed/yfoY53QXEnI" },
      { type: "YouTube", url: "https://www.youtube.com/embed/OXGznpKZ_sA" },
      { type: "YouTube", url: "https://www.youtube.com/embed/1Rs2ND1ryYc" },
      { type: "YouTube", url: "https://www.youtube.com/embed/6vJ8g0d3b9k" },
    ],
  },
  {
    id: 3,
    category: "JavaScript",
    title: "JavaScript Fundamentals",
    description: "Understand JavaScript concepts and start coding interactive websites.",
    videos: [
      { type: "YouTube", url: "https://www.youtube.com/embed/hdI2bqOjy3c" },
      { type: "YouTube", url: "https://www.youtube.com/embed/W6NZfCO5SIk" },
      { type: "YouTube", url: "https://www.youtube.com/embed/6mM2g0d3b9k" },
      { type: "YouTube", url: "https://www.youtube.com/embed/PKwu15ldZ7k" },
    ],
  },
  {
    id: 4,
    category: "JavaScript",
    title: "Advanced JavaScript",
    description: "Deep dive into JavaScript ES6+ features and best practices.",
    videos: [
      { type: "YouTube", url: "https://www.youtube.com/embed/NCwa_xi0Uuc" },
      { type: "YouTube", url: "https://www.youtube.com/embed/8aGhZQkoFbQ" },
      { type: "YouTube", url: "https://www.youtube.com/embed/2LeqilZ5riI" },
      { type: "YouTube", url: "https://www.youtube.com/embed/2Vv-BfVoq4g" },
    ],
  },
  {
    id: 5,
    category: "Git & GitHub",
    title: "Version Control with Git & GitHub",
    description: "Master version control and collaborative development with Git and GitHub.",
    videos: [
      { type: "YouTube", url: "https://www.youtube.com/embed/RGOj5yH7evk" },
      { type: "YouTube", url: "https://www.youtube.com/embed/sw0OW7gHEVg" },
      { type: "YouTube", url: "https://www.youtube.com/embed/1ffBJ4sVUb4" },
    ],
  },
];

function Tutorial() {
  const [selectedCategory, setSelectedCategory] = useState("All");

  const categories = ["All", ...new Set(tutorials.map(t => t.category))];

  const filteredTutorials =
    selectedCategory === "All"
      ? tutorials
      : tutorials.filter(t => t.category === selectedCategory);

  return (
    <div className="tutorial-container">
      <h1>Tutorials</h1>

      <div className="filter-bar">
        <label htmlFor="category">Filter by Category: </label>
        <select
          id="category"
          onChange={(e) => setSelectedCategory(e.target.value)}
          value={selectedCategory}
        >
          {categories.map((cat, i) => (
            <option key={i} value={cat}>
              {cat}
            </option>
          ))}
        </select>
      </div>

      {filteredTutorials.map((tutorial) => (
        <div key={tutorial.id} className="tutorial-card">
          <h2>{tutorial.title}</h2>
          <p>{tutorial.description}</p>
          <div className="video-container">
            {tutorial.videos.map((video, index) => (
              <iframe
                key={index}
                width="100%"
                height="315"
                src={video.url}
                title={`Tutorial Video ${index + 1}`}
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}

export default Tutorial;
